import React from 'react'

export const TodoItem = ({todo, onDelete}) => {
    let sno ='sno'+todo.sno;
    let target = '#sno'+todo.sno;
    return (
        
        <div className="card">
            <div className="card-header" id="headingOne">
            <h2 className="mb-0">
                <button className="btn btn-link" type="button" data-toggle="collapse" data-target={target}  aria-controls="collapseOne">
                {todo.title} {todo.sno}
                </button>
            </h2>
            </div>

            <div id={sno} className="collapse" aria-labelledby="headingOne" data-parent="#accordionExample">
            <div className="card-body">
            {todo.desc}
            <button className="btn btn-sm btn-danger float-right" onClick={ ()=>{onDelete(todo)}}>Delete</button>

                        </div>
            </div>
        </div>


          
       
    )
}
