import React, { useState } from 'react';



export const AddTodo = (props) => {
    const  [title, setTitle] = useState("");
    const  [desc, setDesc] = useState("");
    const submit =(e)=>{
        e.preventDefault();
        if(!title || !desc){
            alert('It cannot be blank');
        }
        else{
            props.addTodo(title, desc);
            setTitle("");
            setDesc("");
            
        }
    }
    return (
        <div className="container shadow p-3 mb-5 bg-white rounded mt-5">
            <h3>Add a Todo</h3>
            <form className="" onSubmit={submit}>
  <div  className="form-group ">
    <label for="exampleInputEmail1">Todo Title</label>
    <input type="title" value={title} className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e)=>{setTitle(e.target.value)}} placeholder="Add Todo"/>
  </div>
  <div className="form-group">
    <label for="exampleInputPassword1">Todo Description</label>
    <input type="text" value={desc} className="form-control" id="exampleInputPassword1" onChange={(e)=>{setDesc(e.target.value)}} placeholder="description"/>
  </div>
 
  <button type="submit" className="btn btn-sm btn-success">Submit</button>
</form>
        </div>
    )
}
